* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.outer-box {
  border: 1vh solid #6491aa;
  width: 100vw;
  height: 100vh;
}

.box {
  border: 2px solid #6491aa;
  height: 96.5vh;
  margin: 0.75vh;
  color: #6c95ad;
  padding: 0 6vw;
}

.date, .today-day {
  display: inline-block;
  width: 50%;
  padding: 6vh 0 3vh 0;
  font-size: 6vw;
}

.today-day {
  text-align: right;
}

.today-date {
  text-align: center;
  font-size: 24vw;
  font-weight: bolder;
}

.progress-text {
  text-align: center;
  margin: 2vh 0;
  font-size: 4vw;
}

.progress {
  background-color: #c9cdd8;
  margin: 3vh 0;
  height: 3vh;
  position: relative;
}

.progress-past {
  position: absolute;
  height: 100%;
  background-color: #6c95ad;
}

.other {
  border: 1px solid #6491aa;
}

.other-title {
  font-size: 4vw;
  height: 20%;
  text-align: center;
}

.other-content {
  font-size: 8vw;
  height: 40%;
  text-align: center;
}

.other-author {
  font-size: 4vw;
  height: 40%;
  text-align: right;
  padding: 0 2vw;
}